// Service de gestion des utilisateurs pour BeaverOS
// Utilise le localStorage pour persister les donnes entre les sessions

// Utilisateur par dfaut (admin/admin)
const DEFAULT_USERS = [
  {
    id: 1,
    username: 'admin',
    password: 'admin',
    displayName: 'Administrateur',
    role: 'admin',
    created: new Date().toISOString(),
    lastLogin: null
  }
];

// Initialiser le stockage des utilisateurs
const initializeUserStorage = () => {
  if (!localStorage.getItem('beaveros-users')) {
    localStorage.setItem('beaveros-users', JSON.stringify(DEFAULT_USERS));
  }
};

// Obtenir tous les utilisateurs
const getAllUsers = () => {
  initializeUserStorage();
  return JSON.parse(localStorage.getItem('beaveros-users') || '[]');
};

// Trouver un utilisateur par nom d'utilisateur
const findUserByUsername = (username) => {
  const users = getAllUsers();
  return users.find(user => user.username.toLowerCase() === username.toLowerCase());
};

// Authentifier un utilisateur
const authenticateUser = (username, password) => {
  const user = findUserByUsername(username);
  
  if (!user) {
    return { success: false, message: 'user_not_found' };
  }
  
  if (user.password !== password) {
    return { success: false, message: 'invalid_password' };
  }
  
  // Mettre  jour la dernire connexion
  updateLastLogin(user.id);
  
  return { 
    success: true, 
    user: {
      id: user.id,
      username: user.username,
      displayName: user.displayName,
      role: user.role
    }
  };
};

// Mettre  jour la dernire connexion
const updateLastLogin = (userId) => {
  const users = getAllUsers();
  const userIndex = users.findIndex(user => user.id === userId);
  
  if (userIndex !== -1) {
    users[userIndex].lastLogin = new Date().toISOString();
    localStorage.setItem('beaveros-users', JSON.stringify(users));
  }
};

// Crer un nouvel utilisateur
const createUser = (userData) => {
  const users = getAllUsers();
  
  // Vrifier si l'utilisateur existe dj
  if (findUserByUsername(userData.username)) {
    return { success: false, message: 'username_exists' };
  }
  
  // Crer le nouvel utilisateur
  const newUser = {
    id: users.length > 0 ? Math.max(...users.map(u => u.id)) + 1 : 1,
    username: userData.username,
    password: userData.password,
    displayName: userData.displayName || userData.username,
    role: userData.role || 'user',
    created: new Date().toISOString(),
    lastLogin: null
  };
  
  users.push(newUser);
  localStorage.setItem('beaveros-users', JSON.stringify(users));
  
  return { 
    success: true, 
    user: {
      id: newUser.id,
      username: newUser.username,
      displayName: newUser.displayName,
      role: newUser.role
    }
  };
};

// Rcuprer les infos de session pour l'utilisateur connect
const getLoggedInUser = () => {
  const userSession = localStorage.getItem('beaveros-session');
  if (!userSession) return null;
  
  try {
    return JSON.parse(userSession);
  } catch (e) {
    return null;
  }
};

// Sauvegarde les info de l'utilisateur connect
const saveLoggedInUser = (userData) => {
  localStorage.setItem('beaveros-session', JSON.stringify(userData));
};

// Effacer les donnes de session
const clearUserSession = () => {
  localStorage.removeItem('beaveros-session');
};

export default {
  authenticateUser,
  findUserByUsername,
  createUser,
  getAllUsers,
  getLoggedInUser,
  saveLoggedInUser,
  clearUserSession
};